import { useBounds } from "@react-three/drei";
import { useThree } from "@react-three/fiber";
import { useStore } from "./store/useStore";
import { useEffect } from "react";
import * as THREE from "three";

export default function ZoomControls({ children }) {
    const api = useBounds();
    const camera = useThree((state) => state.camera);
    const controls = useStore((state) => state.controls);

    useEffect(() => {
        // Rotate on controls change
        if (controls.rotation != 0) {
            const direction = controls.rotation;
            const target = api.getSize().center;
            const angle = THREE.MathUtils.degToRad(direction * 45);
            const rotationAxis = new THREE.Vector3(0, 1, 0); // Rotation around the y-axis

            const position = camera.position.clone(); // Make a copy of the current camera position
            const step = position.clone().sub(target);
            step.applyAxisAngle(rotationAxis, angle); // Rotate around the y-axis
            const newPosition = target.clone().add(step);

            api.to({
                position: [newPosition.x, newPosition.y, newPosition.z],
                target: [target.x, target.y, target.z],
            });

            // Reset values
            useStore.setState({ controls: { rotation: 0 } });
        }
    }, [controls]);

    const handleClick = (e) => {
        useStore.setState({ selected: true });

        e.stopPropagation();
        e.delta <= 2 && api.refresh(e.object).fit();
    };

    const handleMiss = (e) => {
        e.button === 0 && api.refresh().fit();

        useStore.setState({ selected: false });
    };

    return (
        <>
            <group
                onClick={handleClick}
                onPointerMissed={handleMiss}
            >
                {children}
            </group>
        </>
    );
}
