import React, { useRef, useState, useEffect } from "react";
import "leaflet/dist/leaflet.css";
import { TileLayer, MapContainer } from "react-leaflet";
import { Html } from "@react-three/drei";
import { latLng2XY, XY2LatLng } from "./utlis";
import { useStore } from "./store/useStore";
import { useFrame, useThree } from "@react-three/fiber";
import * as THREE from "three";

export default function Basemap() {
    const position4326 = useStore((state) => state.map.center);
    const scaleStep = useStore((state) => state.scaleStep);
    const zoomStep = useStore((state) => state.zoomStep);
    const [mapCenter, setMapCenter] = useState([latLng2XY(position4326)]);
    const [w, setW] = useState(1);
    const [h, setH] = useState(1);
    const { camera, size } = useThree();
    const mapRef = useRef(null);
    const htmlRef = useRef(null);
    const boundingBox = new THREE.Box3();
    const [mapKey, setMapKey] = useState(0); // Add a state variable for the key

    // Update the target point's XZ coordinates ref on each frame
    useFrame(() => {
        const cameraPosition = camera.position;
        const cameraDirection = new THREE.Vector3();
        camera.getWorldDirection(cameraDirection);

        const plane = new THREE.Plane(new THREE.Vector3(0, 1, 0), 0); // XZ plane
        const raycaster = new THREE.Raycaster(cameraPosition, cameraDirection);
        const intersection = new THREE.Vector3();

        raycaster.ray.intersectPlane(plane, intersection);

        const targetX = intersection.x;
        const targetZ = intersection.z;
        setMapCenter([targetX, targetZ]);
    });

    // Update the map's center on each frame
    useEffect(() => {
        if (mapRef.current) {
            mapRef.current.setView(XY2LatLng(mapCenter), zoomStep);
        }
    }, [mapCenter]);

    useEffect(() => {
        // Increment the mapKey when zoomStep changes, triggering a re-render
        setMapKey((prevKey) => prevKey + 1);
        console.log("sd");
    }, [zoomStep]);

    const scaleMatrix = [10 * scaleStep, 10 * scaleStep, 1];

    return (
        <Html
            ref={htmlRef}
            style={{ pointerEvents: "none" }} // Disable pointer events
            rotation={[-Math.PI / 2, 0, -Math.PI / 2]}
            occlude="blending"
            zIndexRange={[1, 0]}
            distanceFactor={96}
            position={[mapCenter[0], 0, mapCenter[1]]}
            scale={scaleMatrix}
            transform
            // material={
            //     <meshStandardMaterial
            //         side={DoubleSide} // Required
            //         transparent
            //         opacity={1} // Degree of influence of lighting on the HTML
            //         color="red"
            //     />
            // }
        >
            <MapContainer
                id="basemap"
                key={mapKey}
                ref={mapRef}
                center={position4326}
                dragging={false}
                scrollWheelZoom={false}
                doubleClickZoom={false}
                zoomControl={false}
                // zoomSnap={0.25}
                zoom={16}
                style={{
                    backgroundColor: "#1a1a1a",
                    height: 1000 * scaleStep,
                    width: 1000 * scaleStep,
                    pointerEvents: "none",
                    position: "relative",
                }}
            >
                <TileLayer
                    url="http://{s}.basemaps.cartocdn.com/dark_nolabels/{z}/{x}/{y}@2x.png"
                    attribution="Map data &copy; OpenStreetMap contributors, under ODbL | Map tiles by &copy; CartoDB, under CC BY 3.0"
                />
            </MapContainer>
        </Html>
    );
}
