//Convert 4236 to 3857
export function latLng2XY([lat, lng]) {
    var pt = L.latLng(lat, lng);
    var projPt = L.CRS.EPSG3857.project(pt);
    return [projPt.y, projPt.x];
}

export function XY2LatLng([x, y]) {
    var projPt = L.point(y, x);
    var latLng = L.CRS.EPSG3857.unproject(projPt);
    return [latLng.lat, latLng.lng];
}

