import { latLng2XY } from "./utlis";

const CubeObj = ({ size, position }) => {
    return (
        <mesh position={position}>
            <boxGeometry args={[size, size, size]} />
            <meshNormalMaterial />
        </mesh>
    );
};

const TorusObj = ({ size, position }) => {
    return (
        <mesh position={position}>
            <torusGeometry args={[size, 30, size, 100]} />
            <meshNormalMaterial />
        </mesh>
    );
};

export default function ExtraGeoms() {
    const pt1 = latLng2XY([41.40551, 2.18763]);
    const pt2 = latLng2XY([41.4001, 2.1812]);
    return (
        <>
            <CubeObj
                size={100}
                position={[pt1[0], 100 / 2, pt1[1]]}
            />
            <TorusObj
                size={100}
                position={[pt2[0], 100, pt2[1]]}
            />
        </>
    );
}
