import React, { useMemo, useRef } from "react";
import { useLoader, useFrame } from "@react-three/fiber";
import { OBJLoader } from "three/examples/jsm/loaders/OBJLoader";
import { latLng2XY } from "./utlis";
import * as THREE from "three";
import { useStore } from "./store/useStore";

export default function Blocks({ path }) {
    const ref = useRef();
    const object = useLoader(OBJLoader, path);
    const position4326 = useStore((state) => state.map.center);

    const position3857 = useMemo(() => latLng2XY(position4326), [position4326]);
    const geometry = useMemo(() => {
        const clonedGeometry = object.children[0].geometry.clone();

        const vertices4326 = clonedGeometry.attributes.position.array;
        const vertices3857 = vertices4326.slice();

        // Modify the vertices progressively
        for (let i = 0; i < vertices3857.length; i += 3) {
            const [x, z] = latLng2XY([-vertices4326[i + 2], vertices4326[i]]);
            const y = vertices4326[i + 1];

            vertices3857[i] = x;
            vertices3857[i + 1] = y / 10;
            vertices3857[i + 2] = z;
        }

        // Replace initial vertices with new ones
        clonedGeometry.setAttribute(
            "position",
            new THREE.BufferAttribute(new Float32Array(vertices3857), 3)
        );
        return clonedGeometry;
    }, [object, position3857]);

    return (
        <mesh
            ref={ref}
            geometry={geometry}
        >
            <meshBasicMaterial color="yellow" />
        </mesh>
    );
}
