import { useStore } from "/store/useStore.jsx";

export default function Scalebar() {
    const zoom = useStore((state) => state.zoom);
    const barWidth = 300;

    const styleContainer = {
        position: "absolute",
        left: "50%",
        bottom: 30,
        margin: "auto",
        width: barWidth,
        height: 100,
        transform: "translate(-50%, 0)",
    };

    const ms = Math.floor(barWidth / zoom);

    const styleBar = {
        position: "absolute",
        left: "50%",
        bottom: 0,
        transform: "translate(-50%, 0)",
        textAlign: "justify",
        textAlignLast: "justify",
        width: 1 * ms * zoom,
        color: "white",
    };

    return (
        <div style={styleContainer}>
            <div style={styleBar}>
                <div
                    style={{
                        marginBottom: 5,
                        height: 1,
                        backgroundColor: "white",
                    }}
                ></div>
                0 {ms}m
            </div>
        </div>
    );
}
