import { OrbitControls, Bounds, OrthographicCamera } from "@react-three/drei";
import { latLng2XY } from "./utlis";
import { useRef, useEffect } from "react";
import { useStore } from "./store/useStore";

import ZoomControls from "./ZoomControls.jsx";
import Basemap from "./Basemap.jsx";
import Blocks from "./Blocks";
import ExtraGeoms from "./ExtraGeoms";

export default function Scene() {
    const origin = useStore((state) => latLng2XY(state.map.center));
    const cameraRef = useRef();
    const selected = useStore((state) => state.selected);
    const detailFactor = useStore((state) => state.detailFactor);
    const camOff = 1000;

    const catchZoom = () => {
        const zoom = cameraRef.current.zoom;

        let mapZoom = 256;
        for (let i = 0; i < 20; i++) {
            if (zoom < mapZoom && zoom > mapZoom / 4) {
                const zoomStep = 20 - i - detailFactor; // 20 is the max zoom level (the world)
                const scaleStep = i - 3 + detailFactor;
                useStore.setState({ zoomStep: zoomStep, scaleStep: scaleStep });
            }
            mapZoom = mapZoom / 4;
        }
        useStore.setState({ zoom: zoom });
    };

    return (
        <>
            <ambientLight />
            <OrthographicCamera
                makeDefault
                position={[origin[0] - camOff, camOff, origin[1]]}
                near={0.1}
                far={50000}
                ref={cameraRef}
            />
            <OrbitControls
                makeDefault
                target={[origin[0] - camOff, camOff, origin[1]]}
                maxPolarAngle={Math.PI / 4}
                minPolarAngle={Math.PI / 40}
                maxAzimuthAngle={-Math.PI / 4}
                minAzimuthAngle={-Math.PI / 2 + 0.01}
                enableDamping={true}
                minZoom={0.2}
                maxZoom={16}
                onChange={() => {
                    catchZoom();
                }}
            />
            <Basemap />

            <Bounds
                fit
                clip
                observe
                margin={selected ? 3 : 1}
            >
                <ZoomControls>
                    <Blocks path={"/models/park_4326_negY.obj"} />
                    <ExtraGeoms />
                </ZoomControls>
            </Bounds>
        </>
    );
}
