import { useStore } from "./store/useStore";
import Stack from "@mui/material/Stack";
import Button from "@mui/material/Button";

export default function RotationButtons() {
    const rotateCamera = (direction) => {
        useStore.setState({ controls: { rotation: direction } });
    };

    const stackStyle = {
        position: "absolute",
        top: 0,
        left: 0,
        zIndex: 99999999,
        margin: 40,
        padding: 10,
        borderRadius: 25,
        backgroundColor: "rgba(0, 0, 0, 1)",
    };

    const btnStyle = {
        borderRadius: 25,
        backgroundColor: "orange",
    };

    return (
        <Stack
            spacing={2}
            style={stackStyle}
            direction="row"
        >
            <Button
                style={btnStyle}
                variant="contained"
                onClick={() => rotateCamera(-1)}
            >
                ←
            </Button>
            <Button
                style={btnStyle}
                variant="contained"
                onClick={() => rotateCamera(1)}
            >
                →
            </Button>
        </Stack>
    );
}
